body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  position: fixed;
  z-index: 9999;
  top: 0;
  width: 100%;
  height: 3em;
  background-color: #0f3457;
  display: grid;
  grid-template-columns: 1fr 2fr 2fr;
  grid-template-rows: 1fr;
  grid-template-areas: "logo title nav"; }

.header-nav {
  grid-area: nav;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "exp pro soc";
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }

.header-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: transparent;
  border: none;
  color: #ebe9e9;
  font-size: 1em;
  text-decoration: none; }

.header-btn:hover {
  background-color: #1e68ae;
  cursor: pointer; }

.exp-btn {
  grid-area: exp; }

.projects-btn {
  grid-area: pro; }

.social-btn {
  grid-area: soc; }

.header-home-btn {
  grid-area: logo; }

.home {
  position: relative;
  min-height: 100vh; }

.title {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #d4d4d4; }

.card-container {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  padding: 5%;
  color: #a3a3a3;
  text-align: center; }

.project-card {
  width: 40em;
  margin: 1em;
  max-height: 40em; }

.card-iframe {
  transform: scale(0.85);
  -webkit-transform: scale(0.85);
  -moz-transform: scale(0.85); }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); }

.modal-main {
  position: fixed;
  background: white;
  width: 80%;
  height: auto;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 9999; }

.display-block {
  display: block; }

.display-none {
  display: none; }

.experience {
  height: calc(100vh - 3em); }

.table-container {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  -webkit-justify-content: center;
          justify-content: center;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0px 10em; }

.cell {
  min-height: 15em; }

.exp {
  border-right: 1px solid #1a344d;
  border-bottom: 1px solid #1a344d; }

.edu {
  border-left: 1px solid #1a344d;
  border-bottom: 1px solid #1a344d; }

.extra {
  border-right: 1px solid #1a344d;
  border-top: 1px solid #1a344d; }

.back {
  border-left: 1px solid #1a344d;
  border-top: 1px solid #1a344d; }

li {
  text-decoration: none;
  list-style: none;
  text-align: left;
  padding: .3em; }

.social {
  width: 100%;
  min-height: 100vh; }
  .social .icon-container {
    min-height: calc(100vh - 3em);
    padding-top: 3em;
    background-color: #0f3457;
    -webkit-animation-name: social-background;
            animation-name: social-background;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center; }
    .social .icon-container a {
      color: #8400ff;
      font-size: 5em;
      padding: 0 5% 1em;
      line-height: 100%;
      opacity: 0;
      -webkit-animation-name: icons-in;
              animation-name: icons-in;
      -webkit-animation-duration: 1.5s;
              animation-duration: 1.5s;
      -webkit-animation-delay: 3s;
              animation-delay: 3s;
      -webkit-animation-timing-function: ease-out;
              animation-timing-function: ease-out;
      -webkit-animation-fill-mode: forwards;
              animation-fill-mode: forwards; }
    .social .icon-container .fa-linkedin:hover {
      color: #0077b5;
      -webkit-transition: color 0.8s;
      transition: color 0.8s; }
    .social .icon-container .fa-github:hover {
      color: #6e5494;
      -webkit-transition: color 0.8s;
      transition: color 0.8s; }
    .social .icon-container .fa-free-code-camp:hover {
      color: #006400;
      -webkit-transition: color 0.8s;
      transition: color 0.8s; }
    .social .icon-container .fa-instagram:hover {
      color: #c13584;
      -webkit-transition: color 0.8s;
      transition: color 0.8s; }
  .social .social-title {
    position: absolute;
    font-size: 3em;
    font-weight: 200;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    -webkit-animation-name: text-up;
            animation-name: text-up;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }

@-webkit-keyframes social-background {
  0% {
    background-color: #0f3457; }
  100% {
    background-color: #3a017c;
    min-height: 18em;
    -webkit-transform: translateY(calc(50% + 3em));
            transform: translateY(calc(50% + 3em)); } }

@keyframes social-background {
  0% {
    background-color: #0f3457; }
  100% {
    background-color: #3a017c;
    min-height: 18em;
    -webkit-transform: translateY(calc(50% + 3em));
            transform: translateY(calc(50% + 3em)); } }

@-webkit-keyframes text-up {
  0% {
    font-size: 3em;
    font-weight: 200;
    top: 50%; }
  50% {
    font-size: 6em;
    font-weight: 300;
    top: 50%; }
  100% {
    font-size: 3em;
    font-weight: 200;
    top: 15%; } }

@keyframes text-up {
  0% {
    font-size: 3em;
    font-weight: 200;
    top: 50%; }
  50% {
    font-size: 6em;
    font-weight: 300;
    top: 50%; }
  100% {
    font-size: 3em;
    font-weight: 200;
    top: 15%; } }

@-webkit-keyframes icons-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes icons-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.App {
  text-align: center;
  line-height: 1.3em;
  color: #d4d4d4; }

section {
  position: relative; }

.particles {
  position: absolute;
  z-index: -10;
  height: 100%;
  width: 100%;
  background-color: #081a2b; }

